import request from '@/utils/request'

// 资讯首页信息
export const handleMediaInfo = (params) => {
  return request(
    'GET',
    '/dapp/information/getInformationAdminIndex',
    params,
    true,
    true,
    1
  )
}

// 机构资讯列表
export const handleAdminNewsList = (params) => {
  return request('GET', '/dapp/information/adminList', params)
}

// 用户资讯列表
export const handleNewsList = (params) => {
  return request('GET', '/dapp/information/list', params, true, true)
}

// 发布资讯
export const handleAddNews = (params) => {
  return request('POST', '/dapp/information', params)
}

// 资讯详情
export const handleNewsDetail = (params) => {
  return request('GET', `/dapp/information/${params}`, params, true, true)
}
// 修改资讯
export const handleEditNews = (params) => {
  return request('PUT', '/dapp/information', params)
}
// 删除资讯
export const handleDeleteNews = (params) => {
  return request('DELETE', `/dapp/information/${params}`)
}
