<template>
  <div class="release-wrap">
    <div class="notice" style="padding: 8px 8px 0 0">
      {{ $t('admin.need') }}
    </div>
    <div class="title-wrap">
      <van-field
        translate="no"
        v-model="dataForm.content"
        rows="6"
        type="textarea"
        maxlength="500"
        placeholder=""
        show-word-limit
      />
    </div>
    <div class="notice">{{ $t('admin.noNeed') }}</div>
    <div class="set-image">
      <van-uploader
        style="width: 100%"
        :after-read="afterRead"
        v-model="fileList"
      >
        <div class="upload">
          <van-icon name="plus" size="25" class="icon" />
          <div class="add">{{ $t('admin.addImg') }}</div>
        </div>
      </van-uploader>
    </div>
    <div class="icon-group">
      <div
        class="icon-normal"
        @click="handleIsImportant"
        v-show="!dataForm.isImportant"
      ></div>
      <div
        class="icon-select"
        @click="handleIsImportant"
        v-show="dataForm.isImportant"
      >
        <div class="dot"></div>
      </div>
      <span>{{ $t('admin.important') }}</span>
    </div>
    <div class="button" @click="handleAddNews">{{ $t('admin.release') }}</div>
  </div>
</template>

<script>
import config from '@/config/app.config'
import { handleAddNews, handleNewsDetail, handleEditNews } from '@/services/news'
import { upload } from '@/services/upload'
export default {
  name: '',
  data () {
    return {
      title: '',
      fileList: [],
      dataForm: {
        content: '',
        type: 0,
        preview: '',
        isImportant: 0
      }

    }
  },
  methods: {
    handleIsImportant () {
      this.dataForm.isImportant = this.dataForm.isImportant === 0 ? 1 : 0
    },
    // 图片上传
    async afterRead (file) {
      this.fileList = []
      // 此时可以自行将文件上传至服务器
      var formdata = new FormData()
      formdata.append('file', file.file)
      const resp = await upload(formdata)
      if (!resp.success) {
        this.fileList = []
        return
      }
      this.fileList.push(file)
      this.dataForm.preview = config.imgUrl + resp.data.url
    },
    // 保存
    async handleAddNews () {
      if (!this.dataForm.content) {
        return this.$toast(this.$t('common.fill'))
      }
      let resp
      if (this.$route.query.id) {
        resp = await handleEditNews(this.dataForm)
      } else {
        resp = await handleAddNews(this.dataForm)
      }
      if (!resp.success) return
      this.$gbUtils.handleToPage('/mediaAdmin')
    },
    // 获取详情
    async getDetail (id) {
      const resp = await handleNewsDetail(id)
      if (!resp.success) return
      this.dataForm = { ...resp.data }
      this.fileList.push({ url: this.dataForm.preview })
    }
  },
  computed: {

  },
  mounted () {
    if (this.$route.query.id) {
      this.getDetail(this.$route.query.id)
    }
  }
}
</script>

